// Link list enkel
// -------------------------
.link_list_simple {
    @include rem('padding-left', 14px);
    @include rem('margin-bottom', 35px);

    a {
        display: block;
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('padding', 7px 0);
    }
}




// Link list
// -------------------------
.link_list {
    list-style: none;
    padding: 0;
    @include clearfix();
    @include rem('margin-bottom', 24px);

    li {
        list-style-image: none;
        @include rem('border-bottom', 1px solid $janna);
    }

    a {
        display: block;
        @include rem('padding', 10px 12px);
        background: url(../images/default_list_style_image.svg) no-repeat transparent;
        @include rem('background-position', 2px 20px);
    }
    
    // with metainfo
    // -------------------------
    &--with_meta {

        a {        
            &:hover,
            &:active {
                text-decoration: none;

                .title {
                    text-decoration: underline;
                }
            }
        }

        .meta {
            color: $emperor;
            @include rem('font-size', 17px);
            line-height: ( 21px / 17px );

            &:before {
                content: " - ";
            }
        }
    }
}


// Link list med toppsak
// -------------------------
.link_list li.important_news_item {
    border-bottom: 0 none;

    a {
        background-image: none; // remove link list arrow
        padding: 0;
    }
}


// Skjema lenke liste i toppen av tjenestebeskrivelse
// ----------------------------------------------------
.application-link-list {
    clear: both;
    padding: 0;

    li {
        list-style: none;
    }
}


// Tjenster og skjema / Skjema og tjenester lister
// ----------------------------------------------------
.service-application-list {
    padding-left: 0;
    
    & > li {
        list-style-image: none;
        @include rem('border', 1px solid $zombie);
        @include rem('margin-bottom', 14px);
        @include border-radius();
        @include clearfix();
    }
        
    .header-link {
        display: block;
        position: relative;
        @include rem('padding', 6px 15px);
        @include border-radius();
        background-color: $rum_swizzle;
    }
    
    // With children
    & > li.has-children .header-link {
        @include rem('border-bottom', 1px solid $zombie);
        @include border-radius($base_border_radius $base_border_radius 0 0);
    }
    
    // Liste til skjema
    .application-link-list {
        margin: 0;
        
        li {
            margin: 0;
            @include rem('border-bottom', 1px solid $janna);
            
            &:last-child {
                border-bottom: 0 none;
            }
        }
    }
    
    // Liste til tjenester
    .link_list {
        margin: 0;
        
        li a {
            @include rem('font-size', 17px);
            line-height: ( 21px / 17px );
            @include rem('padding', 14px 12px 13px 27px);
            @include rem('background-position', 17px 20px);
        }
        
        li:last-child {
            border-bottom: 0 none;
        }
    }
    
    // Hvis tittel er en skjema lenke
    .header-link.application-link {
        @include rem('padding-left', 44px);
        
        &:before {
            @include rem('left', 8px);
            @include rem('top', 4px);
        }
    }
}



// Arbeidsplaner liste
// -------------------------------------------
.schedules_list_0 {
    padding-left: 0;
    
    & > li {
        list-style-image: none;
        @include rem('border', 1px solid $zombie);
        @include rem('margin-bottom', 20px);
        @include border-radius();
        @include clearfix();
        
        .schedules_list_header_1 {
            @include SourceSansProRegular();
            @include rem('font-size', 19px);
            line-height: ( 28px / 19px );
            display: block;
            position: relative;
            margin: 0;
            @include rem('padding', 6px 15px);
            @include border-radius();
            background-color: $rum_swizzle;
            @include rem('border-bottom', 1px solid $zombie);
            @include border-radius($base_border_radius $base_border_radius 0 0);
        }
        
        .schedules_list_header_2,
        .schedules_list_header_3,
        .schedules_list_header_4 {
            @include SourceSansProRegular();
            @include rem('font-size', 19px);
            line-height: ( 28px / 19px );
            @include rem('margin', 7px 0 0 16px);
        }
    }
    
    .schedules_list_1 {        
        clear: both;
        padding: 0;
        @include rem('margin', 6px 0 8px);

        li {
            list-style: none;
            margin: 0;
        }
        
        .schedules_list_item_1 {
            @include rem('border-bottom', 1px solid $janna);
            @include rem('margin-bottom', 6px);
            
            &:last-child {
                border-bottom: 0 none;
            }
        }     
        
        .schedules_list_2,
        .schedules_list_3,
        .schedules_list_4 {
            @include rem('padding-left', 13px);
        }   
    }
    
    .file-link {
        @include rem('margin', 3px 0 3px 12px);
    }
}
