.category_navigation_container {
    @include rem('margin-bottom', 120px);

    @media only screen and (min-width: $md_break) {
        @include rem('min-height', 130px);
        @include rem('margin-bottom', 60px);
    }

    @media only screen and (min-width: $lg_break) {
        @include rem('margin-bottom', 80px);
    }
}

// Tjenestekategori 1
// ---------------------------------------------------

.category_navigation_1 {
    padding: 0;
    margin: 0;

    li {
        list-style: none;
        @include rem('margin-bottom', 4px);
    }

    li a {
        position: relative;
        display: block;
        color: $black;
        position: relative;
        @include rem('font-size', 20px);
        line-height: (28px / 20px);
        @include rem('padding', 10px 40px 11px 14px);
        @include rem('border-left', 4px solid $main_red);
        background-color: $janna;
        @include hyphens();

        .arrow-icon {
            @include single-arrow-icon($direction: 'down', $color: $main_red);
            position: absolute;
            @include rem('top', 21px);
            @include rem('right', 16px);
        }

        .arrow-icon--2 {
            @include single-arrow-icon($direction: 'down', $color: $main_red);
            position: absolute;
            @include rem('top', 27px);
            @include rem('right', 16px);
        }
    }

    li.active > a,
    li.active > .category_navigation--editable__flex-container > a {
        color: $white;
        text-decoration: none;
        background-color: $main_red;
        @include rem('margin-bottom', 4px);

        .arrow-icon {
            @include single-arrow-icon($direction: 'up', $color: $white);
            position: absolute;
            @include rem('top', 21px);
            @include rem('right', 16px);
        }

        .arrow-icon--2 {
            @include single-arrow-icon($direction: 'up', $color: $white);
            position: absolute;
            @include rem('top', 27px);
            @include rem('right', 16px);
        }
    }

    // Tablet
    @media only screen and (min-width: $md_break) {

        li a .arrow-icon {
            @include single-arrow-icon($direction: 'down', $color: $main_red);
            position: absolute;
            @include rem('top', 24px);
            @include rem('right', 16px);
        }
        li a .arrow-icon--2 {
            display: none;
        }

        li.active > a .arrow-icon {
            @include single-arrow-icon($direction: 'up', $color: $white);
            position: absolute;
            @include rem('top', 24px);
            @include rem('right', 16px);
        }
        li.active > a .arrow-icon--2 {
            display: none;
        }
    }

    // Desktop
    @media only screen and (min-width: $lg_break) {

        & > li > a,
        & > li > .category_navigation--editable__flex-container > a {
            border-left-color: $main_red;

            .arrow-icon {
                @include single-arrow-icon($direction: 'right', $color: $main_red);
                position: absolute;
                @include rem('top', 17px);
                @include rem('right', 19px);
            }

            .arrow-icon--2 {
                @include single-arrow-icon($direction: 'right', $color: $main_red);
                position: absolute;
                @include rem('top', 17px);
                @include rem('right', 25px);
            }
        }

        li.active > a,
        li.active > .category_navigation--editable__flex-container > a {
            background: url(../images/tjenester_bg.svg) no-repeat center right $main_red;
            background-size: cover;
            margin-bottom: 0;

            .arrow-icon,
            .arrow-icon--2 {
                display: none;
            }
        }

    }
}

// Tjenestekategori 2
// ---------------------------------------------------

.category_navigation_2 {
    display: none;
    list-style: none;
    padding: 0;
    @include rem('margin-bottom', 10px);

    &.active {
        display: inherit;
    }

    li a {
        position: relative;
        border-left: 0 none;
        background-color: $rum_swizzle;
        @include rem('padding-left', 16px);

        .arrow-icon {
            @include single-arrow-icon($direction: 'down', $color: $main_red);
            position: absolute;
            @include rem('top', 24px);
            @include rem('right', 16px);
        }
    }

    li.active > a,
    li.active > .category_navigation--editable__flex-container > a {
        color: $link_color;
        background-color: $rum_swizzle;
        @include rem('border-bottom', 1px solid $zombie);
    }

    li.active > a .arrow-icon,
    li.active > .category_navigation--editable__flex-container > a .arrow-icon {
        @include single-arrow-icon($direction: 'up', $color: $danube);
        position: absolute;
        @include rem('top', 24px);
        @include rem('right', 16px);
    }

    // Tablet
    @media only screen and (min-width: $md_break) {

        li a .arrow-icon {
            @include single-arrow-icon($direction: 'right', $color: $main_red);
            position: absolute;
            @include rem('top', 17px);
            @include rem('right', 22px);
        }

        li.active > a,
        li.active > .category_navigation--editable__flex-container > a {
            color: $white;
            background: url(../images/tjenester_bg.svg) no-repeat center right $main_red;
            background-size: cover;
            border-bottom: 0 none;
        }

        li.active > a .arrow-icon,
        li.active > .category_navigation--editable__flex-container > a .arrow-icon {
            display: none;
        }
    }

    // Desktop
    @media only screen and (min-width: $lg_break) {
        & > li > a,
        & > li > .category_navigation--editable__flex-container > a {
            @include rem('padding-left', 14px);
            @include rem('border-left', 4px solid $main_red);
        }
    }
}

// Redigering av kategorier
.category_navigation--editable {

    form {
        display: flex;
        width: 100%;

        &.hidden {
            display: none;
        }
    }

    input {
        @include rem('margin-left', 4px);
    }

    button {
        margin: 0;
        border-radius: 0;
        @include rem('padding', 1px);

        .grunt-icon {
            @include rem('margin', 0px auto 0);
        }
    }

    li {
        &.add_new {
            @include rem('border', 1px dashed $link_color);
            @include border-radius();
            @include rem('height', 49px);

            form {
                height: 100%;
            }

            a {
                background-color: transparent;
                border: none;
                @include rem('padding-left', 40px);

                .plus-arrow-icon {
                    position: absolute;
                    @include rem('top', 25px);
                    @include rem('left', 16px);
                    @include plus-icon();
                }

                &:hover,
                &:active {
                    background-color: transparent;
                }
            }
        }

        &.edit {
            border: none;
        }

        .category_navigation--editable__flex-container {
            @include flexbox();
            @include flex-flow($values: (row nowrap));
            @include rem('min-height', 49px);
            width: 100%;

            form {
                width: 90%
            }

            a.title {
                width: 80%;
            }

            a.edit-button {
                @include rem('height', 50px);
                @include rem('padding', 0px);
                border-left: none;
                width: 10%;
                height: inherit;
                background: $white_sand !important;

                .grunt-icon {
                    @include rem('margin', 8px auto 0);
                }

                &:hover,
                &:active {
                    background: $alto !important;
                }
            }

            div.edit-button--disabled {
                min-width: 10%;
                background-color: $white_sand !important;
            }
        }
    }
}

// Tjenestekategori 3
// ---------------------------------------------------

.category_navigation_3 {
    display: none;
    list-style: none;
    @include rem('margin', 0 0 6px 10px);
    padding: 0;

    &.active {
        display: inherit;
    }

    li {
        @include rem('border-bottom', 1px solid $zombie);

        &:last-child {
            border-bottom: 0 none;
        }

        &.add_new {
            @include rem('border', 1px dashed $link_color);
            @include border-radius();

            &:before {
                // Hide ul 1px border on tablet
                position: absolute;
                @include rem('top', -2px);
                @include rem('left', 0px);
                content: "";
                width: 100%;
                @include rem('height', 1px);
                background-color: $white;
            }
        }
    }

    li a {
        background-color: transparent;
        @include border-radius();
        margin-bottom: 0;
        @include rem('padding-left', 8px);

        .arrow-icon {
            @include single-arrow-icon($direction: 'right', $color: $danube);
            position: absolute;
            @include rem('top', 17px);
            @include rem('right', 25px);
        }

        .arrow-icon--2 {
            @include single-arrow-icon($direction: 'right', $color: $danube);
            position: absolute;
            @include rem('top', 17px);
            @include rem('right', 19px);
        }

        &:link,
        &:visited,
        &:active,
        &:focus,
        &:hover {
            color: $link_color;
        }

        &:hover,
        &:active {
            color: $link_color_hover;
            background-color: transparent;
            text-decoration: underline;
        }
    }

    // Tablet
    @media only screen and (min-width: $md_break) {
        @include rem('border-top', 1px solid $zombie);
        @include rem('border-bottom', 1px solid $zombie);

        li a {
            @include rem('padding-left', 18px);

            .arrow-icon {
                @include single-arrow-icon($direction: 'right', $color: $danube);
                position: absolute;
                @include rem('top', 17px);
                @include rem('right', 24px);
            }

            .arrow-icon--2 {
                @include single-arrow-icon($direction: 'right', $color: $danube);
                position: absolute;
                @include rem('top', 17px);
                @include rem('right', 18px);
            }
        }
    }

    // Desktop
    @media only screen and (min-width: $lg_break) {
        border-top: 0 none;
        border-bottom: 0 none;

        li {
            border-bottom: 0 none;

            a {
                background-color: $white_sand;
                @include rem('padding-left', 38px);
                @include rem('padding-right', 12px);

                .arrow-icon {
                    @include single-arrow-icon($direction: 'right', $color: $danube);
                    position: absolute;
                    right: inherit;
                    @include rem('top', 17px);
                    @include rem('left', 20px);
                }

                .arrow-icon--2 {
                    @include single-arrow-icon($direction: 'right', $color: $danube);
                    position: absolute;
                    right: inherit;
                    @include rem('top', 17px);
                    @include rem('left', 26px);
                }

                &:hover,
                &:active {
                    background-color: $white_sand; // important to repeat that
                }
            }
        }
    }
}


// Layout changes on bigger screens
// ---------------------------------------------------

@media only screen and (max-width: $md_break_max) {
}

@media only screen and (min-width: $md_break) {
    .category_navigation_1 {
        position: relative;
        z-index: 10;
    }
    .category_navigation_3 {
        position: absolute;
        top: 0;
        left: 100%;
        width: 100%;
        @include rem('margin-left', 30px);
    }
}

@media only screen and (min-width: $md_break) and (max-width: $lg_break_max) {
    .category_navigation_2 li {
        position: relative;
    }
}

@media only screen and (min-width: $lg_break) {
    .category_navigation_2 {
        position: absolute;
        top: 0;
        left: 100%;
        width: 100%;
        @include rem('margin-left', 30px);
        @include rem('min-height', 75px);
    }
}


// Tjenestekategori // Grey info boxes
// ---------------------------------------------------
@media only screen and (min-width: $md_break) {
    // Some min-heights for the infos
    .info_emneord_tjeneste,
    .info_tjeneste,
    .info_tjeneste > li > ul {

    }
    // Common for box and tekst
    .info_emneord_tjeneste:before,
    .info_emneord_tjeneste:after,
    .info_tjeneste > li > ul:before,
    .info_tjeneste > li > ul:after {
        position: absolute;
        left: 100%;
        width: 100%;
        height: 100%;
        @include rem('margin-left', 30px);
        @include box-sizing();
    }
    // Grey boks
    .info_emneord_tjeneste:before,
    .info_tjeneste > li > ul:before {
        top: 0;
        z-index: 0;
        content: "";
        background-color: $white_sand;
        @include rem('border', 1px solid $alto);
        @include border-radius();
        @include rem('min-height', 130px);
    }
    // Tekst
    .info_emneord_tjeneste:after,
    .info_tjeneste > li > ul:after {
        top: 25%;
        z-index: 1;
        text-align: center;
        @include rem('padding', 0 40px);
        content: "Velg kategori og underkategori til venstre for å se tjenester";
    }
}

@media only screen and (min-width: $lg_break) {
    // On bigger screens, change text
    .info_emneord_tjeneste:after {
        content: "Velg kategori til venstre for å se dens underkategorier";
    }
    .info_tjeneste > li > ul:after {
        content: "Velg underkategori til venstre for å se underliggende tjenester";
    }
    // On bigger screens, add empty box on the right
    .info_emneord_tjeneste > li:first-child:before {
        position: absolute;
        top: 0;
        left: 200%;
        z-index: 0;
        content: "";
        width: 100%;
        height: 100%;
        @include box-sizing();
        @include rem('margin-left', 60px);
        @include rem('border', 1px solid $alto);
        @include border-radius();
    }
}


