// Teaser = a piece that raises interest, without intending to satisfy
// Before we used ece_module_container for the same purpose
// Now there is just one link with all the content inside
// ----------------------------------------------------------

.teaser {
    width: 100%;
    @include clearfix();

    &__link {
        display: block;
        width: 100%;

        &:focus {
            outline: 0 none;
        }

        &:hover,
        &:active {
            text-decoration: none;
        }

        &:focus .teaser__title {
            @include outline();
        }

        &:hover .teaser__title,
        &:active .teaser__title {
            color: $link_color_hover;
            text-decoration: underline;
        }
    }

    &__image-container {
        display: block;
        width: 100%;
        @include rem('margin-bottom', 14px);
    }

    &__image {
        display: block;
        width: 100%;
    }

    &__content-container {
        h1, h2, h3, h4, h5, h6 {
            margin-top: 0;
        }
    }

    &__title {
        display: block;
        @include rem('font-size', 22px);

        @media (min-width: $md_break) {
            @include rem('font-size', 30px);
        }

        line-height: ( 28px / 22px );
        @include SourceSansProRegular();
    }

    &__date {
        color: $text_color;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
    }

    &__excerpt {
        color: $text_color;

        p {
            @include rem('margin-bottom', 14px);
        }
    }

    &--50-50-desktop {
        @media (min-width: $md_break) {
            .teaser__image-container {
                float: left;
                width: 48%;
                @include rem('margin', 4px 28px 14px 0);
            }
        }
    }

    // Box style
    &--box-style {
        display: block;
        @include rem('padding', 21px 21px 7px);
        @include rem('border', 1px solid $zombie);
        background-color: $rum_swizzle;
        @include border-radius();
        @include box-sizing();
        @include rem('margin-bottom', 6px);
        @include hyphens();
        @include clearfix();

        .teaser__excerpt {
            @include rem('margin-bottom', 14px);
        }
    }

    // Equal heights for the teaser link element
    &--equal-heights .teaser__link {
        @media (min-width: $md_break) {
            @include flexbox();

            .teaser__image,
            .teaser__content-container {
                // Make a floated div 100% height of its parent
                @include align-items();
                @include flex();
            }
        }
    }
}





