.handlekurv {
  position: sticky;
  top: 40px;
  z-index: 999;
  background-color: white;
}

@media (max-width: $lg_break) {
  .handlekurv {
	position: sticky;
	bottom: 0;
	overflow:auto;
	max-height: 300px;
  }
}

.farge-animasjon {
  animation-name: yellowfade;
  animation-duration: 1.5s;
}

@keyframes yellowfade {
  from { background: #ffe87c; }
  to { background: transparent; }
}

.handlekurv_container {
  width: 100%;
  @include rem('padding', 21px 21px 7px);
  @include rem('border', 1px solid $zombie);
  @include border-radius();
  @include box-sizing();
  @include rem('margin-bottom', 28px);
  @include hyphens();

  .handlekurv_header {
	@include SourceSansProRegular();
	@include rem('font-size', 24px);
	line-height: ( 28px / 24px );
	margin-top: 0;
	@include rem('margin-bottom', 14px);
  }

  .handlekurv_excerpt {
	@include rem('margin-bottom', 14px);

	ul {
	  @include rem('margin-bottom', 14px);
	}

	a {
	  text-decoration: underline;
	}

	a.button {
	  text-decoration: none;
	}

	ul {
	  padding-left: 0;

	  li {
		list-style-image: none;
		@include rem('padding', 10px 0 10px 14px);
		@include rem('border-bottom', 1px solid $janna);
		@include rem('background-position', 1px 20px);

		&:last-child {
		  border-bottom: 0 none;
		}
	  }

	  a {
		text-decoration: none;

		&:hover,
		&:active,
		&:focus {
		  text-decoration: underline;
		}
	  }
	}
  }
}

.handlekurv_container .handlekurv_excerpt .handlekurv_list {
  @include rem('border-bottom', 1px solid $janna);
  margin-bottom: 0;
}

.handlekurv_container .handlekurv_excerpt .handlekurv_list li {
  background-image: none;
  padding-left: 0;

  .handlekurv_tekst {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
  }

  a:focus,
  a:hover,
  a:active {
	text-decoration: none;
  }
}

